<template>
  <div :class="['toggle', { 'toggle--checked': modelValue, 'toggle--disabled': disabled }]" @click="onToggle">
    <div :class="['toggle__inner', { 'toggle__inner--checked': modelValue, 'toggle__inner--disabled': disabled }]" />
  </div>
</template>

<script setup lang="ts">
import { bool } from "vue-types";
const props = defineProps({
  modelValue: bool().def(false),
  disabled: bool().def(false),
});

const emit = defineEmits<{
  (event: "update:modelValue", value: typeof props.modelValue): void;
}>();

const onToggle = () => {
  if (!props.disabled) {
    emit("update:modelValue", !props.modelValue);
  }
};
</script>

<style lang="scss" scoped>
.toggle {
  position: relative;

  display: flex;
  align-items: center;
  justify-content: flex-start;

  width: 30px;
  height: 20px;
  padding: 5px;

  cursor: pointer;
  transition: all var(--transition);

  border: 1px solid var(--light-grey);
  border-radius: 10px;
  background-color: var(--blue-alise);

  &--disabled {
    cursor: default;
  }

  &--checked {
    border-color: var(--brandeis-blue);
    background-color: var(--brandeis-blue);

    &.toggle--disabled {
      border-color: var(--light-grey);
      background-color: var(--light-grey);
    }
  }
}

.toggle__inner {
  position: absolute;
  left: 5px;

  width: 10px;
  height: 10px;

  transition: all var(--transition);

  border-radius: 50%;
  background-color: var(--brandeis-blue);
  box-shadow: var(--toggle-shadow);

  &--disabled {
    background-color: var(--silver-chalice);
  }

  &--checked {
    left: calc(100% - 15px);

    background-color: var(--blue-alise);

    &.toggle__inner--disabled {
      background-color: var(--white);
    }
  }
}
</style>
